import { Component, OnInit, Output } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { routerTransition } from '../../router.animations';
import { Encounter } from '../../encounter/masters/encounter';
import { EncounterService } from '../../encounter/services/encounter.service';
import { Patient } from '../../patient/master/patient';

@Component({
	selector: 'app-start-consultation',
	templateUrl: './start-consultation.component.html',
	styleUrls: ['./start-consultation.component.css'],
	animations: [routerTransition()]
})
export class StartConsultationComponent implements OnInit {
	encounterList: Array<Encounter>;
	encounter: Encounter = new Encounter();

	encounterId: number;

	@Output() patientList;
	@Output() patient: Patient = new Patient();
	constructor(private encounterService: EncounterService, private route: ActivatedRoute) { }

	ngOnInit(): void {
		this.encounterId = Number(sessionStorage.getItem('encounterId'));
		this.encounterId = this.route.snapshot.params['id'];
		//console.log(this.encounterId);
		this.reloadData();
	}

	reloadData() {
		this.encounterService.getEncounterById(this.encounterId).subscribe(data => {
			this.encounter = data;
			this.patient = this.encounter.appointment.patient;
			console.log("Patient from consultant", this.patient)
		})


	}
	callPatientDetails(patient){
		console.log(patient);
	}

}
